.Dog-details {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-radius: 2rem;
	background-color: rgba(246, 244, 244, 0.928);
	width: 50rem;
	box-shadow: 5px 10px 18px #888888;
}

.Dog-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-top: 1rem;
}

.Dog-list {
	list-style-type: none;
	margin: 0 auto;
	padding: 2rem;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-radius: 2rem;
	height: 10rem;
	width: 15rem;
	background-color: rgba(246, 244, 244, 0.928);
	box-shadow: 5px 10px 18px #888888;
}

.Dog-list a {
	font-size: 1.5rem;
	text-decoration: none;
}

.Dogs {
	padding-top: 1rem;
}

.Logo {
	width: 1.5rem;
	height: 1.5rem;
}

